import React, {useState} from 'react'
import '../App.css'

const initialState = {
    runtime: '',
    packageNameZip:'',
    s3Bucket:  '',
    module: ''
}

export default function WebForm() {
    const [form,setForm] = useState(initialState)

    function SubmitForm(event) {        
        setForm(initialState)        
        event.preventDefault()
    }
    function handleChange(event) {
        const {name,value} =  event.target
        setForm(previousEntries => {
            console.log('Previous entries look like:', previousEntries)
            return {
                ...previousEntries,
                [name]:value
            }
        })

    }
    
    return (
     
      <div className='container'>
            <section id='container-base'>
                <div align='center' id='deployment'>
                <form>

                <label for="runtime">Choose a runtime:</label>
                        <select required="true" onChange={handleChange} id="runtime" name="runtime">
                            <option value=""></option>
                            <option name="python3.8">Python3.8</option>
                            <option name="python3.7">Python3.7</option>
                            <option name="python3.6">Python3.6</option>
                            <option name="node">Node</option>
                        </select>
                <br/>
                <br/>
                    <p>
                        <label for="packageNameZip">Save Deployment As:</label>
                        <input  onChange={handleChange} id='packageNameZip' type="text" name="packageNameZip" 
                        value={form.packageNameZip}
                        placeholder="Enter zipfile name" />
                    </p>

                    <p>
                        <label for="S3Bucket">Store in S3 bucket:</label>
                        <input  onChange={handleChange} id='deploymentPackage' type="text" name="s3Bucket" 
                        value={form.s3Bucket} placeholder="Enter S3 Bucket name" />
                    </p>

                    <p>
                        <label for="Modules">Package:</label>
                        <input  onChange={handleChange} id='Module' type="text" name="module"
                        value={form.module}
                         placeholder="Enter Module to install " />
                    </p>
                
                <br/>
                    <button type="submit" 
                    
                    action="http://0.0.0.0:8080/" method="post"
                    onSubmit={SubmitForm}> Create Deployment Package </button>                

                </form>

                </div>
            </section>

      </div>
    )
}
